.skillsBlock{
    min-height: 100vh;
    background: yellow;
}
.skillsContainer{
    flex-direction: column;



}
.skillsWrapper{
    width: 100%;
}
.skillsTitle{
    text-align: center;
    padding: 20px;
}

.skills{
    display: flex;
    justify-content: space-between;

    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 20px;

}

@media (max-height:776px){
    .skills{
        justify-content: center;
    }

}